import {jsxs as _jsxs, jsx as _jsx} from "react/jsx-runtime";import {css, StyleSheet} from "aphrodite";

import {Component} from "react";
import AutoSizer from "react-virtualized-auto-sizer";

import Editor from "./Editor";
import FallbackEditor from "./FallbackEditor";
















export default class EditorWrapper extends Component {constructor(...args) { super(...args); EditorWrapper.prototype.__init.call(this);EditorWrapper.prototype.__init2.call(this);EditorWrapper.prototype.__init3.call(this); }
  __init() {this.state = {
    MonacoEditor: null,
  }}

  __init2() {this.editor = null}

  async componentDidUpdate(prevProps) {
    if (this.props.babelLoaded && !this.state.MonacoEditor) {
      this.setState({MonacoEditor: (await import("react-monaco-editor")).default});
    }
  }

  __init3() {this.invalidate = () => {
    if (this.editor) {
      this.editor.invalidate();
    }
  }}

  _formatTime() {
    const {timeMs} = this.props;
    if (timeMs == null) {
      return "";
    } else if (timeMs === "LOADING") {
      return " (...)";
    } else {
      return ` (${Math.round(timeMs * 100) / 100}ms)`;
    }
  }

  render() {
    const {MonacoEditor} = this.state;
    const {label, code, onChange, isReadOnly, isPlaintext, options} = this.props;
    return (
      _jsxs('div', { className: css(styles.editor), children: [
        _jsxs('span', { className: css(styles.label), children: [
          label
          , this._formatTime()
        ]})
        , _jsx('span', { className: css(styles.container), children: 
          _jsx(AutoSizer, { onResize: this.invalidate, defaultWidth: 300, defaultHeight: 300, children: 
            ({width, height}) =>
              MonacoEditor ? (
                _jsx(Editor, {
                  ref: (e) => {
                    this.editor = e;
                  },
                  MonacoEditor: MonacoEditor,
                  width: width,
                  height: height - 30,
                  code: code,
                  onChange: onChange,
                  isPlaintext: isPlaintext,
                  isReadOnly: isReadOnly,
                  options: options,}
                )
              ) : (
                _jsx(FallbackEditor, {
                  width: width,
                  height: height - 30,
                  code: code,
                  onChange: onChange,
                  isReadOnly: isReadOnly,}
                )
              )
            
          })
        })
      ]})
    );
  }
}

const styles = StyleSheet.create({
  editor: {
    display: "flex",
    flexDirection: "column",
    minWidth: 300,
    height: "100%",
    flex: 1,
    // When adding a third editor, we need the container size to shrink so that
    // the Monaco layout code will adjust to the container size.
    overflowX: "hidden",
    margin: 8,
  },
  label: {
    color: "white",
    lineHeight: "30px",
    padding: 8,
  },
  container: {
    height: "100%",
  },
});
